@import url(https://fonts.googleapis.com/css?family=Roboto:400,500&display=swap);
*{box-sizing:border-box}*:before,*:after{box-sizing:border-box}html,body{height:100%;position:relative}body{background:#fff;color:#2b2b2b;margin:0;padding:0;font-family:'Roboto', sans-serif}h3{text-transform:uppercase}header nav{height:9.3vh;line-height:88px;background:#ffb347;background:-webkit-gradient(linear, left top, right top, from(#fc3), to(#ffb347));background:linear-gradient(to right, #fc3, #ffb347)}header nav ul a{color:#fff;font-size:3em;text-transform:uppercase}header nav .brand-logo img{width:80%;padding:0}.gray-bg{background:#eee}ul#page-numbers{text-align:center}ul#page-numbers li{display:inline-block;padding:10px}footer.page-footer{background-color:#000001 !important;text-align:center;padding:10px}footer.page-footer h5{text-transform:uppercase}footer.page-footer p{text-align:justify}footer.page-footer ul li{margin:10px 0 10px 0}footer.page-footer ul li a{text-transform:uppercase}@media only screen and (max-width: 768px){header nav{height:10vh}.movielist .row .col.s6{width:46%}.movielist .row .col.m6{width:100%}header nav .brand-logo img{width:100%;padding:7px 19px 5px 20px;background:#fff}}

